import * as Mui from '@material-ui/core';
import React from 'react';

interface CartState {
	items: number;
}

interface CartProps {
	price: number;
}

export class Cart extends React.Component<CartProps, CartState> {
	constructor(props: CartProps) {
		super(props);
		this.state = {
			items: 0,
		};
		this.changeItemCount = this.changeItemCount.bind(this);
	}

	changeItemCount(): void {
		this.setState({ items: this.state.items + 1 });
	}

	render(): JSX.Element {
		return (
			<div className="cart">
				<div>
					<Mui.TextField variant="outlined" type="number" label="Items" onChange={this.changeItemCount} value={this.state.items} />
				</div>
				<div>
					<Mui.Typography variant="caption">Price per item: ${this.props.price}</Mui.Typography>
				</div>
				<div>
					<Mui.TextField
						variant="outlined"
						type="text"
						label="Total"
						disabled={true}
						value={this.state.items * this.props.price}
						InputProps={{
							startAdornment: <Mui.InputAdornment position="start">$</Mui.InputAdornment>
						}}
						/>
				</div>
			</div>
		);
	}
}
